// home 1
.masco--Digital-agency {
    margin-top: 15px
}
.masco--hero-section{
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    margin-top: -80px;
    padding-top: 185px;
    .masco--hero-content{
        h1{
            @include max(991px) {
                margin: 0 100px 20px;
                text-align: center;
            }
            @include max(767px) {
                margin: 0 70px 20px;
                text-align: center;
            }
        }
        p{
            font-family: $body-font;
            font-weight: 400;
            font-size: 18px;
            line-height: 30px;
            color: var(--gray-900);
            opacity: 0.8;
            margin-bottom: 50px;
            margin-right: 30px;
            @include max(1199px) {
                margin: 0;
            }
            @include max(991px) {
                text-align: center;
            }
        }
        .masco--subscription-box{
            margin-bottom: 20px;
            margin-right: 200px;
            @include max(1399px) {
                margin-right: 160px;
            }
            @include max(1199px) {
                margin-right: 80px;
            }
            @include max(991px) {
                margin: 0 30px 20px;
            }
            position: relative;
            input[type=email] {
                border: 1px solid var(--gray-900) !important;
                border-radius: 50px;
            }
            a{
                position: absolute;
                top: 5px;
                right: 6px;
                height: 50px;
                line-height: 11px;
            }
        }
    }
    .masco--hero-video{
        position: relative;
        .masco--hero-img{
            object-fit: cover;
            object-position: center center;
            text-align: right;
            @include max(991px) {
                text-align: center;
            }
        }
        .masco--hero-playbtn{
            position: absolute;
            top: 50%;
            right: 45%;
            transform: translate(50%,-50%);
            background-color: var(--warning-700);
            width: 100px;
            height: 100px;
            text-align: center;
            line-height: 105px;
            border-radius: 50%;
            @include max(1399px) {
                right: 48%;
            }
            a{
                display: block;
            }
        }
    }
}


// home 2
.masco--chat-software {
    margin-top: 30px;
}
.home2{
    .masco--trial-box {
        margin-bottom: 24px;
        a{
            display: block;
        }
    }
    .masco--icon-list {
        img{
            width: 24px;
            height: 24px;
        }
        .text-opacity{
            opacity: 1;
            font-family: $body-font;
            font-weight: 600;
            font-size: 16px;
            line-height: 24px;
            color: var(--gray-900);
        }
    }
    .masco--hero-img {
        text-align: right;
        margin-top: -45px;
    }
    .masco--readmore-btn {
        margin-bottom: 24px;
        margin-left: 20px;
        padding: 18px 30px;
    }
}


// home 3
.masco--fitness-app {
    margin-top: 15px;
}
.home3{
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    margin-top: -85px;
    padding-top: 180px;
    .masco--hero-content {
        p{
            opacity: 0.8
        }
        .masco--cutomers-rating{
            display: flex;
            .masco--home3-hero-customers {
                display: flex;
                justify-content: space-between;
                a{
                    margin-top: 10px;
                }
                .masco--customer-content{
                    margin-left: 10px;
                    h3{
                        font-family: $body-font;
                        letter-spacing: -1px;
                    }
                    p{
                        font-family: $body-font;
                        font-weight: 400;
                        font-size: 16px;
                        color: var(--gray-900);
                    }
                }
            }
            .masco--home3-hero-rating {
                padding-left: 23px;
                &::before{
                    content: '';
                    width: 2px;
                    height: 39px;
                    background-color: var(--gray-900);
                }
            }
        }
    }
    .masco--hero-img {
        text-align: center;
        position: relative;
        .masco--hero-main-img {
            z-index: 3;
        }
        .masco--home3-hero-shape1 {
            position: absolute;
            bottom: 17%;
            left: 12%;
            z-index: 1;
        }
        .masco--home3-hero-shape2 {
            position: absolute;
            top: 15%;
            right: -7%;
            z-index: 2;
        }
    }
}