// cta 1
.masco--cta-area {
    padding-top: 120px;
    border-bottom: 1px solid var(--warning-700);
    position: relative;
    background-color: var(--gray-900);
    .masco--cta-title {
        margin: 0 60px 80px 0;
        @include max(767px) {
            margin: 0 60px;
            text-align: center;
        }
        h2{
            color: var(--warning-700);
        }
    }
    .masco--cta-btn{
        text-align: right;
        margin-top: 35px;
        @include max(767px) {
            margin: 35px;
            text-align: center;
        }
        .fugu-btn {
            a{
                color: var(--warning-700);
            }
        }
    }
    .masco--cta-img {
        position: absolute;
        top: 9%;
        right: 9%;
        @include max(1399px) {
            right: 1%;
        }
        @include max(767px) {
            right: 22%;
            top: 50%;
        }
        @include max(575px) {
            right: 14%;
        }
        img{
            @include max(767px) {
                width: 75px;
            }
        }
    }
}



// cta 2
.masco--cta2-section {
    background-color: var(--pink-400);
    position: relative;
    .masco--cta2-title {
        text-align: center;
        margin: 0 300px 50px;
        @include max(1399px) {
            margin: 0 200px 50px;
        }
        @include max(1199px) {
            margin: 0 120px 50px;
        }
        @include max(991px) {
            margin: 0 40px 30px;
        }
        @include max(767px) {
            margin: 25px 10px;
        }
        h2{
            color: var(--warning-700);
        }
    }
    .masco--cta2-leftimg {
        position: absolute;
        top: 12%;
        left: 5%;
        @include max(575px) {
            display: none;
        }
    }
    .masco--cta2-rightimg {
        position: absolute;
        bottom: 0;
        right: 3%;
        @include max(575px) {
            display: none;
        }
    }
}


// cta 3
.masco--cta3-section {
    padding-top: 180px;
    @include max(991px) {
        text-align: center;
    }
    .yellow-cta {
        background-color: var(--warning-550);
        border-radius: 50px;
        align-items: center;
    }
    .masco--cta3-image {
        position: relative;
    }
    .masco--cta3-mobile-img {
        position: absolute;
        top: -11.5%;
        left: 14%;
        @include max(1199px) {
            top: -31%;
        }
        @include max(991px) {
            top: -11.5%;
            left: 25%;
        }
        @include max(767px) {
            top: -15%;
            left: 18%;
        }
        @include max(575px) {
            top: -11%;
        }
    }
    .masco--cta3-content {
        margin: 30px 0;
        p{
            font-family: $body-font;
            font-weight: 400;
            font-size: 18px;
            line-height: 30px;
            color: var(--gray-900);
            opacity: 0.8;
            margin-top: 20px;
            margin-bottom: 50px;
        }
    }
}