.masco--faq-area {
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    .masco--faq-title {
        text-align: center;
        margin: 0 320px 80px;
        @include max(1399px) {
            margin: 0 240px 80px;
        }
        @include max(1199px) {
            margin: 0 150px 80px;
        }
        @include max(991px) {
            margin: 0 75px 50px;
        }
        @include max(767px) {
            margin: 40px;
        }
    }
    .accordion {
        margin-bottom: 80px;
    }
    .accordion-button{
        background-color: var(--warning-700);
        color: var(--gray-900);
        font-family: $body-font;
        font-weight: 600;
        font-size: 20px;
        line-height: 28px;
    }
    .accordion-header{
        padding: 15px;
    }
    .masco--faq-btn{
        display: block;
        margin:  0 auto;
        a{
            color: var(--warning-700);
        }
    }
}