// footer 1
.fugu-footer-section{
    padding-top: 120px;
    padding-bottom: 85px;
    background-color: var(--gray-900);
    .fugu-textarea {
        p{
            margin-top: 30px;
            font-family: $body-font;
            font-weight: 400;
            font-size: 18px;
            line-height: 30px;
            color: var(--warning-700);
            opacity: 0.8;
            @include max(1399px) {
                margin: 10px 0;
            }
        }
        .footer-mail {
            margin-bottom: 24px;
            @include max(1399px) {
                margin-bottom: 10px;
            }
        }
    }
    .fugu-social-icon {
        @include max(991px) {
            margin-bottom: 20px;
        }
        @include max(575px) {
            margin-bottom: 40px;
        }
        ul{
            display: flex;
            li{
                width: 30px;
                height: 30px;
                margin-right: 15px;
                a{
                    display: block;
                    background-color: rgba(255, 255, 255, 0.1);
                    text-align: center;
                    border-radius: 50%;
                    &:hover{
                        background-color: var(--primary-700);
                        transition: all 0.3s ease;
                    }
                }
            }
        }
    }
    .fugu-footer-menu {
        @include max(575px) {
            margin-bottom: 40px;
        }
        span{
            color: var(--warning-700);
            margin-bottom: 30px;
            font-family: $body-font;
            font-weight: 600;
            font-size: 20px;
            line-height: 28px;
        }
        ul{
            margin-top: 30px;
            @include max(575px) {
                margin-top: 15px;
            }
            li{
                margin-top: 10px;
                a{
                    color: var(--warning-700);
                    opacity: 0.8;
                    font-family: $body-font;
                    font-weight: 400;
                    font-size: 18px;
                    line-height: 30px;
                }
            }
        }
    }
}

.masco--copyright-section {
    background-color: var(--gray-900);
    border-top: 1px solid rgba(255,255,255,0.05);
    .masco--copyright-area {
        padding: 18px 0;
        text-align: center;
        a{
            font-family: $body-font;
            font-weight: 400;
            font-size: 16px;
            line-height: 24px;
            color: #fff;
            opacity: 0.8;
        }
    }
}



