// portfolio 1
.masco--portfolio-section {
        background-position: center center;
        background-repeat: no-repeat;
        background-size: cover;
        .masco--portfolio-title {
            margin: 0 60px 80px 0;
            @include max(767px) {
                margin: 0;
                text-align: center;
            }
        }
        .masco--portfolio-btn{
            text-align: right;
            margin-top: 54px;
            margin-right: 20px;
            @include max(1199px) {
                margin: 40px 0 0 0;
            }
            @include max(767px) {
                margin: 15px 0;
                text-align: center;
            }
            .fugu-btn {
                a{
                    color: var(--warning-700);
                }
            }
        }
        .masco--single-portfolio {
            @include max(767px) {
                margin-bottom: 30px;
            }
            img{
                border-radius: 10px;
                margin-bottom: 24px;
            }
            .masco--single-portfolio-text {
                display: flex;
                justify-content: space-between;
                margin-right: 16px;
                @include max(1399px) {
                    margin-right: 0;
                }
                p{
                    font-family: $body-font;
                    font-weight: 600;
                    font-size: 24px;
                    line-height: 32px;
                    letter-spacing: -0.5px;
                    color: var(--gray-900);
                }
                span{
                    font-family: $body-font;
                    font-weight: 400;
                    font-size: 18px;
                    line-height: 30px;
                    color: var(--gray-900);
                }
            }
            .read-more-btn{
                    img{
                        margin-bottom: 0;
                   } 
            }
        }
}



// portfolio 2
.masco--portfolio2-section {
    .masco--portfolio2-title {
        text-align: center;
        margin: 0 322px;
        @include max(1399px) {
            margin: 0 235px;
        }
        @include max(1199px) {
            margin: 0 145px;
        }
        @include max(991px) {
            margin: 0 25px;
        }
        @include max(767px) {
            margin: 0;
        }
        p{
            margin-top: 20px;
            margin-bottom: 80px;
        }
    }
    .masco--portfolio2-video {
        position: relative;
        .masco--hero-img{
            object-fit: cover;
            object-position: center center;
        }
    }
    .masco--portfolio2-playbtn {
            position: absolute;
            top: 50%;
            right: 50%;
            transform: translate(50%,-50%);
            background-color: var(--warning-700);
            width: 100px;
            height: 100px;
            text-align: center;
            line-height: 105px;
            border-radius: 50%;
            a{
                display: block;
            }
    }
}