// feature 1
.masco--feature-area {
    background-color: rgba(162, 89, 255, 0.03);
    @media (max-width: 991px){
    padding: 40px;
}
    .masco--feature-title {
        margin: 0 340px;
        @include max(1399px) {
            margin: 0 260px;
        }
        @include max(1199px) {
            margin: 0 165px;
        }
        @include max(991px) {
            margin: 0 85px;
        }
        @include max(767px) {
            margin: 0 45px;
        }
        @include max(575px) {
            margin: 0 15px;
        }
        h2{
            text-align: center;
            margin-bottom: 65px;
        }
    }
    .masco--single-feature {
        border: 2px solid var(--gray-900);
        border-radius: 10px;
        padding: 30px;
        &:hover{
            box-shadow: 8px 8px;
            transition: all 0.3s ease;
        }
        @include max(991px) {
            margin-bottom: 20px;
        }
        h4{
            margin-top: 30px;
            letter-spacing: -0.5px;
        }
        p{
            font-family: $body-font;
            font-weight: 400;
            font-size: 18px;
            line-height: 30px;
            color: var(--gray-900);
            opacity: 0.8;
            margin-bottom: 0;
        }
    }
}



// feature 2
.masco--feature2-area {
    .masco--feature2-title {
        text-align: center;
        margin: 0 350px 80px;
    }
    .masco--single-feature2 {
        display: flex;
        margin-right: 70px;
        .masco--single-feature2-img {
            margin-right: 24px;
            img{
                object-fit: cover;
                object-position: center center;
            }
        }
        .masco--feature2-content {
            p{
                font-family: $body-font;
                font-weight: 400;
                font-size: 18px;
                line-height: 30px;
                color: var(--gray-900);
                opacity: 0.8;
            }
        }
    }
}