// testimonial 1
.masco--testimonial-area {
    background-color: var(--warning-900);
    .masco--testimonial-title {
        margin: 0 330px 80px;
        text-align: center;
        @include max(1399px) {
            margin: 0 230px 80px;
        }
        @include max(1199px) {
            margin: 0 140px 80px;
        }
        @include max(991px) {
            margin: 0 80px 80px;
        }
        @include max(767px) {
            margin: 30px;
        }
    }
    .masco--testimonial-img {
        @include max(991px) {
            text-align: center;
            margin-bottom: 50px;
        }
        img{
            object-fit: cover;
            object-position: center top;
        }
    }
    .masco--testimonial-content {
        margin-left: 79px;
        @include max(1399px) {
            margin-left: 20px;
        }
        @include max(1199px) {
            margin-left: 10px;
        }
        img{
            object-fit: cover;
            object-position: center top;
        }
        p{
            font-family: $body-font;
            font-weight: 600;
            font-size: 24px;
            line-height: 32px;
            letter-spacing: -0.5px;
            color: var(--gray-900);
            margin: 30px 0;
        }
        .testimonial-identity {
            margin-bottom: 50px;
            @include max(991px) {
                margin-bottom: 20px;
            }
            p{
                font-family: $body-font;
                font-weight: 600;
                font-size: 20px;
                line-height: 28px;
                color: var(--gray-900);
                margin: 0;
            }
            span{
                font-family: $body-font;
                font-weight: 600;
                font-size: 20px;
                line-height: 28px;
                color: var(--gray-900);
                opacity: 0.8;
            }
        }
    }
}


// testimonial 2

.masco--testimonial2-section {
    background-color: rgba(162, 89, 255, 0.03);
    .masco--testimionial2-title {
        text-align: center;
        margin: 0 320px;
        margin-bottom: 80px;
        @include max(1399px) {
            margin: 0 300px;
        }
        @include max(1199px) {
            margin: 0 215px;
        }
        @include max(991px) {
            margin: 80px 115px;
        }
        @include max(767px) {
            margin: 80px 85px;
        }
        @include max(575px) {
            margin: 40px 80px;
        }
    }
    .masco--single-testimonial2 {
        @include max(991px) {
            margin-bottom: 30px;
        }
        text-align: center;
        p{
            font-family: $body-font;
            font-weight: 400;
            font-size: 18px;
            line-height: 30px;
            color: var(--gray-900);
            margin-top: 30px;
            opacity: 1;
        }
        span{
            color: var(--gray-900); 
        }
    }
    .masco--testimonial2-btn {
        margin-top: 80px;
    }
    
}


// testimonial 3
.masco--testimonial3-section {
    background-color: var(--warning-900);
    .masco--testimonial3-title{
        margin-bottom: 80px;
    }
    .masco--testimonial3-btn {
        margin-top: 40px;
    }
    .masco--single-testimonial3 {
        background-color: var(--warning-700);
        border-radius: 10px;
        padding: 30px;
        margin-bottom: 24px;
        .masco--single-testimonial3-images {
            margin-bottom: 30px;
        }
        span{
            font-family: $body-font;
            font-weight: 600;
            font-size: 18px;
            line-height: 30px;
            color: var(--gray-900);
        }
        p{
            margin-top: 5px;
            margin-bottom: 24px;
            font-family: $body-font;
            font-weight: 400;
            font-size: 18px;
            line-height: 30px;
            color: var(--gray-900);
        }
        .masco--single-testimonial3-content {
            display: flex;
            .masco--single-testimonial3-img {
                margin-right: 15px;
            }
            .masco--single-testimonial3-text {
                span{
                    margin-bottom: 5px;
                    font-family: $body-font;
                    font-weight: 600;
                    font-size: 18px;
                    line-height: 30px;
                    color: var(--gray-900);
                }
                p{
                    font-family: $body-font;
                    font-weight: 400;
                    font-size: 14px;
                    line-height: 20px;
                    color: var(--gray-900);
                    opacity: 0.7;
                }
            }
        }
    }
}