.masco--pricing-section {
    background-color: rgba(162, 89, 255, 0.03);
    .masco--pricing-title {
        text-align: center;
        margin: 0 350px 80px;
        @include max(1399px) {
            margin: 0 280px 80px;
        }
        @include max(1199px) {
            margin: 0 190px 80px;
        }
        @include max(991px) {
            margin: 40px 100px;
        }
        @include max(767px) {
            margin: 40px;
        }
    }
    .masco--pricing-box {
        background-color: var(--warning-700);
        border: 2px solid var(--gray-900);
        border-radius: 10px;
        padding: 40px;
        @include max(991px) {
            margin-bottom: 20px;
        }
        &:hover{
            box-shadow: 8px 8px var(--gray-900);
        }
        .masco--pricing-name {
            text-align: center;
            margin-bottom: 20px;
        }
        .masco--price-set {
            display: flex;
            justify-content: center;
            .masco--pricing-currency {
                h6{
                    font-family: $body-font;
                    font-weight: 400;
                    font-size: 28px;
                    line-height: 80px;
                    color: var(--gray-900);
                }
            }
            .masco--pricing-time {
                h6{
                    font-family: $body-font;
                    font-weight: 400;
                    font-size: 28px;
                    line-height: 80px;
                    color: var(--gray-900);
                }
            }
        }
        .masco--pricing-description {
            padding-bottom: 40px;
            border-bottom: 1px solid var(--gray-900);
            p{
                font-family: $body-font;
                font-weight: 400;
                font-size: 14px;
                line-height: 20px;
                color: var(--gray-900);
                opacity: 0.7;
                text-align: center;
            }
        }
        .masco--pricing-feature {
            padding-top: 40px;
            padding-left: 20px;
            .masco--icon-list {
                ul{
                    li{
                        p{
                            font-family: $body-font;
                            font-weight: 600;
                            font-size: 16px;
                            line-height: 24px;
                            color: var(--gray-900);
                            margin-bottom: 24px;
                            opacity: 1;
                        }
                        .opacity-2{
                            opacity: 0.2;
                        }
                    }
                }
            }
        }
        .masco--pricing-btn {
            a{
                display: block;
                padding: 0 57px;
                @include max(1399px) {
                    padding: 0;
                }
            }
        }
    }
}