.masco--icon-list{
    @include max(991px) {
        margin: 0 150px;
    }
    @include max(991px) {
        margin: 0 60px;
    }
    li{
        @include max(991px) {
            margin-bottom: 20px;
        }
        display: flex;
        img{
            margin-right: 7px;
            margin-bottom: 50px;
            @include max(1199px) {
                margin-bottom: 0;
            }
        }
        p{
            font-family: $body-font;
            font-weight: 400;
            font-size: 14px;
            line-height: 20px;
            color: var(--gray-900);
            opacity: 0.5;
        }
    }
}