.masco--integration-section {
    @include max(991px) {
        text-align: center;
    }
    .masco--integration-content {
        @include max(991px) {
            margin-bottom: 20px;
        }
        p{
            margin-bottom: 50px;
            font-family: $body-font;
            font-weight: 400;
            font-size: 18px;
            line-height: 30px;
            color: var(--gray-900);
            opacity: 0.8;
        }
    }
    .masco--integration-image {
        text-align: right;
        img{
            margin: 0 30px 30px 0;
        }
        @include max(991px) {
            text-align: center;
        }
    }
}