.masco--logo-area{
    .masco--brandlogo-text{
        margin-top: 100px;
        margin-bottom: 80px;
        @include max(991px) {
            margin-bottom: 50px;
        }
        p{
            text-align: center;
            font-family: $body-font;
            font-weight: 600;
            font-size: 20px;
            line-height: 28px;
            color: var(--gray-900);
            opacity: 0.7;
        }
    }
    .masco--brand-logo{
        .logo-thumbnail{
            margin-bottom: 102px;
            img{
                margin-right: 70px;
                @include max(1399px) {
                    margin-right: 35px;
                }
                @include max(1199px) {
                    margin-right: -1px;
                }
                @include max(991px) {
                    margin-bottom: 15px;
                }
            }
        }
    }
}