.masco--service-area{
    .masco--service-title{
        margin: 0 320px 80px;
        @include max(1399px) {
            margin: 0 250px 80px;
        }
        @include max(1199px) {
            margin: 0 165px 80px;
        }
        @include max(991px) {
            margin: 0 90px 80px;
        }
        @include max(767px) {
            margin: 0 40px 80px;
        }
        text-align: center;
    }
    .masco--single-service{
        &:hover {
            background-color: var(--warning-900);
        }
        border: 1px solid var(--warning-800);
        border-radius: 10px;
        padding: 40px;
        @include max(1399px) {
            padding: 18px;
        }
        @include max(1199px) {
            padding: 20px;
        }
        margin-bottom: 24px;
        display: flex;
        .masco--service-img{
            margin-right: 40px;
            @include max(1399px) {
                margin-right: 15px;
                padding: 30px 0;
            }
            @include max(1199px) {
                margin-right: 15px;
                padding: 60px 0;
            }
            @include max(991px) {
                margin-right: 40px;
                padding: 0;
            }
            @include max(767px) {
                margin-right: 10px;
                padding: 40px 0;
            }
            img{
                object-fit: cover;
                object-position: center center;
                width: 190px;
                @include max(1399px) {
                    width: 250px;
                }
                @include max(1199px) {
                    width: 270px;
                }
                @include max(991px) {
                    width: 190px;
                }
                @include max(767px) {
                    width: 220px;
                }
            }
        }
        .masco-service-content {
            h4{
                font-family: $body-font;
                line-height: 32px;
                letter-spacing: -0.5px;
            }
            p{
                font-family: $body-font;
                font-weight: 400;
                font-size: 18px;
                line-height: 30px;
                color: var(--gray-900);
                opacity: 0.8;
            }
            .read-more-btn{
                a{
                    color: var(--gray-900);
                    &:hover{
                        color: var(--primary-700);
                    }
                    span{
                        margin-right: 10px;
                    }
                    img{
                        margin-bottom: 0;
                   } 
                }
            }
        }
    }
}