// content box 1
.masco--content-box {
    background-color: var(--warning-900);
    padding-bottom: 0;
    .masco--content-box-text {
        @include max(1399px) {
            margin-top: 20px;
        }
        @include max(1199px) {
            text-align: center;
            margin-bottom: 100px;
        }
        h2{
            margin-bottom: 20px;
        }
        p{
            font-family: $body-font;
            font-weight: 400;
            font-size: 18px;
            line-height: 30px;
            color: var(--gray-900);
            opacity: 0.8;
        }
        span{
            font-family: $body-font;
            font-weight: 400;
            font-size: 18px;
            color: var(--gray-900);
            opacity: 0.8;
            display: block;
        }
    }
    .masco--content-image {
        position: relative;
        .masco--first-content-img {
            @include max(1199px) {
                text-align: center;
            }
            img{
                object-fit: cover;
                object-position: center top;
            }
        }
        .masco--first-content-shape {
            position: absolute;
            top: -10%;
            right: 7%;
            @include max(1399px) {
                right: -7%;
            }
            @include max(1199px) {
                right: 15%;
            }
            @include max(991px) {
                right: 3%;
            }
            @include max(767px) {
                right: -12%;
            }
            @include max(575px) {
                right: -8%;
            }
        }
    }
    .masco--content-img {
        position: relative;
        @include max(1399px) {
            margin-top: 30px;
        }
        .masco--2nd-content-img {
            @include max(1199px) {
                text-align: center;
            }
            img{
                object-fit: cover;
                object-position: center top;
            }
        }
        .masco--2nd-content-shape {
            position: absolute;
            bottom: -2%;
            left: -7%;
            @include max(1199px) {
                left: 18%;
            }
            @include max(991px) {
                left: 8%;
            }
            @include max(767px) {
                left: -6%;
            }
            @include max(575px) {
                left: -3%;
            }
        }
    }
    .masco--2nd-content-box-text{
        @include max(1399px) {
            margin-top: 45px;
        }
        @include max(1199px) {
            text-align: center;
        }
        .masco--icon-list {
            @include max(991px) {
                margin: 0;
            }
            li{
                @include max(1199px) {
                    margin-left: 180px;
                }
                @include max(991px) {
                    margin-left: 60px;
                }
                @include max(767px) {
                    margin: 0;
                }
                img{
                    margin-bottom: 10px;
                    @include max(767px) {
                        margin-bottom: 40px;
                    }
                }
                p{
                    font-family: $body-font;
                    font-weight: 600;
                    font-size: 18px;
                    line-height: 30px;
                    color: var(--gray-900);
                    margin-bottom: 10px;
                    opacity: 1;
                    text-align: start;
                }
            }
        }
    }
    .content-text1{
        padding-bottom: 50px;
        border-bottom: 1px solid rgba(0, 16, 47, 0.1);
    }
    .masco--content-btn {
        margin-top: 50px;
        @include max(1199px) {
            text-align: center;
        }
    }
}


// content box 2
.content-box2 {
    .masco--content-img {
        margin-top: 40px;
        @include max(1199px) {
            text-align: center;
        }
    }
    .masco--2nd-content-box-text {
        margin-top: 40px;
        @include max(1199px) {
            text-align: center;
        }
    }
    .masco--content-box-text {
        margin-bottom: 0;
        margin-top: 150px;
        @include max(1199px) {
            text-align: center;
        }
    }
    .masco--content-image {
        margin-bottom: 0;
        margin-top: 150px;
        @include max(1199px) {
            text-align: center;
        }
    }
}


// content box 3
.content-box3{
    .masco--content-img {
        @include max(1199px) {
            text-align: center;
        }
    }
    .masco--content-image {
        text-align: right;
        @include max(1199px) {
            text-align: center;
            margin: 50px;
        }
    }
    .masco--content-box-text {
        @include max(1199px) {
            margin-bottom: 0;
        }
    }
}