.masco--video-section {
    position: relative;
    .masco--video-title {
        text-align: center;
        margin: 0 320px 80px;
        @include max(1399px) {
            margin: 0 240px 80px;
        }
        @include max(1199px) {
            margin: 0 150px 50px;
        }
        @include max(991px) {
            margin: 0 70px 50px;
        }
        @include max(767px) {
            margin: 20px;
        }
    }
    .masco--single-video-section {
        @include max(991px) {
            text-align: center;
        }
        .masco--video-image {
            position: relative;
            img{
                object-fit: cover;
                object-position: center top;
            }
            .masco-video-play-btn {
                position: absolute;
                top: 50%;
                right: 50%;
                transform: translate(50%,-50%);
            }
        }
        h4{
            margin-top: 24px;
            margin-bottom: 15px;
        }
        p{
            font-family: $body-font;
            font-weight: 400;
            font-size: 18px;
            line-height: 30px;
            color: var(--gray-900);
            opacity: 0.8;
            margin-right: 45px;
            @include max(991px) {
                margin: 0 130px 20px;
            }
            @include max(767px) {
                margin: 0 50px 50px;
            }
        }
    }
    .masco--video-btn {
        margin-top: 80px;
        @include max(991px) {
            margin-top: 30px;
        }
    }
    .masco--video-shape {
        position: absolute;
        top: 18%;
        @include max(575px) {
            opacity: 0;
        }
    }
}